.main {
  box-sizing: border-box;
  margin: 0;
  padding: 80px 15px;
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  color: white;
  font-weight: 300;
  background: #50a3a2;
  width: 100%;
  height: 100%;
}

.main__title {
  font-size: 40px;
  font-weight: 200;
}
.main__button {
  padding: 15px 25px;
  color: #fff;
  background: lightblue;
}
.main__cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  height: 100%;
  padding: 15px 0;
  font-weight: 300;
  font-family: "Source Sans Pro", sans-serif;

  list-style: none;
}
